
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';

const images = [
    { id: '1', src: './images/image1.jpg', text: 'Частный дом/квартира' },
    { id: '2', src: './images/image2.jpg', text: 'Кафе/ Точка фастфуда' },
    { id: '3', src: './images/image3.jpg', text: 'СТО/ Автомойка' },
    { id: '4', src: './images/image4.jpg', text: 'Магазин/ салон красоты' },
    { id: '5', src: './images/image5.jpg', text: 'Склад/Офис' },
    { id: '6', src: './images/image6.jpg', text: 'Производство/ гаражный кооператив ' }
]

const FirstStepContent = ({ value, onClick }) => {
    return (
        <Grid item>
            <div className="question">Где будет использоваться видеонаблюдение?</div>
            <div className="completeTextContainer">Выберете тип вашего объекта и нажмите Далее</div>
            <Grid container justifyContent="center" spacing={2}>
                {images.map(({ id, src, text }) => (
                    <Grid xs={6} sm={4} key={id} item>
                        <div onClick={() => onClick({ ...value, firstStep: images.find(({ id: imageId }) => imageId === id).text })} className="card">
                            {value.firstStep === text && <CheckCircleIcon color="primary" fontSize="large" className="checkIcon" />}
                            <img className="image" src={src} alt={src} />
                        </div>
                        <div>{text}</div>
                    </Grid>
                ))}
            </Grid>
        </Grid>
    )
}

export default FirstStepContent