
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';

const images = [
    { id: '1', src: './images/sms.png', text: 'SMS' },
    { id: '2', src: './images/telegr.jpg', text: 'Telegram' },
    { id: '3', src: './images/viber.jpg', text: 'Viber' },
]

const ThirdStepContent = ({ value, onClick }) => {
    return (
        <Grid item>
            <div className="question">Какой вариант для вас подходит?</div>
            <div className="completeTextContainer">По соотношению цена/качество </div>
            <Grid container justifyContent="center" spacing={2}>
                {images.map(({ id, src, text }) => (
                    <Grid xs={6} sm={4} key={id} item>
                        <div onClick={() => onClick({ ...value, thirdStep: images.find(({ id: imageId }) => imageId === id).text })} className="card">
                            {value.thirdStep === text && <CheckCircleIcon color="primary" fontSize="large" className="checkIcon" />}
                            <img className="contactImage" src={src} alt={src} />
                        </div>
                        <div>{text}</div>
                    </Grid>
                ))}
            </Grid>
        </Grid>
    )
}

export default ThirdStepContent