
import React from 'react';
import Grid from '@material-ui/core/Grid';
import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'



const FourthStepContent = ({ value: stateValue, onFinishInput }) => {

    const [value, setValue] = React.useState('');

    const handleChange = (phone) => {
        console.log(phone.length)
        if (phone.length === 12) {
            onFinishInput({ ...stateValue, fourthStep: phone })
        } else if ('phone.length === 10') {
            onFinishInput({ ...stateValue, fourthStep: '' })
        }
        setValue(phone)
    };
    return (
        <Grid item>
            <div className="question">Укажите номер, привязанный к вашему мессенджеру</div>
            <div className="completeTextContainer">Мы с Вами свяжемся, если возникнут вопросы</div>
            <div className="wrapInput">
                <PhoneInput
                    containerStyle={{ width: '70%' }}
                    country="by"
                    inputStyle={{ width: '100%' }}
                    value={value}
                    onChange={handleChange}
                />
            </div>
        </Grid>
    )
}

export default FourthStepContent;