import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import FirstStepContent from './FirstStepContent'
import SecondStepContent from './SecondStepContent'
import ThirdStepContent from './ThirdStepContent'
import FourthStepContent from './FourthStepContent'

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    backButton: {
        marginRight: theme.spacing(1),
    },
    instructions: {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
    },
}));

function getSteps() {
    return ['Выберите место использования видеонаблюдения', 'Выберите соотношение цена/качество', 'Способ связи', 'Обратная связь'];
}

export default function HorizontalLabelPositionBelowStepper() {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);
    const [data, setData] = React.useState({ firstStep: '', secondStep: '', thirdStep: '', fourthStep: '' });
    const steps = getSteps();

    const handleClick = (state) => setData(state)

    function getStepContent(stepIndex) {
        switch (stepIndex) {
            case 0:
                return <FirstStepContent value={data} onClick={handleClick} />;
            case 1:
                return <SecondStepContent value={data} onChange={handleClick} />;
            case 2:
                return <ThirdStepContent value={data} onClick={handleClick} />;
            case 3:
                return <FourthStepContent value={data} onFinishInput={handleClick} />;
            default:
                return 'Unknown stepIndex';
        }
    }


    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
        if (activeStep === 3) console.log('finish', data)
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleReset = () => {
        setData({ firstStep: '', secondStep: '', thirdStep: '', fourthStep: '' })
        setActiveStep(0);
    };

    return (
        <div className={classes.root}>
            <Stepper activeStep={activeStep} style={{ backgroundColor: 'transparent' }} alternativeLabel>
                {steps.map((label) => (
                    <Step key={label}>
                        <StepLabel>{label}</StepLabel>
                    </Step>
                ))}
            </Stepper>
            <div>
                {activeStep === steps.length ? (
                    <div>
                        <div className="completeTextContainer"><Typography className="completeText">Ваша заявка принята!</Typography></div>
                        <Button onClick={handleReset}>Сбросить и пройти заново</Button>
                    </div>
                ) : (
                    <div>
                        <Typography className={classes.instructions}>{getStepContent(activeStep)}</Typography>
                        <div className="buttons">
                            <Button
                                disabled={activeStep === 0}
                                onClick={handleBack}
                                className={classes.backButton}
                            >
                                Back
                            </Button>
                            <Button disabled={!Object.values(data)[activeStep]} variant="contained" color="primary" onClick={handleNext}>
                                {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                            </Button>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
