
import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';

const SecondStepContent = ({ value, onChange }) => {

    const handleChange = (event) => {
        onChange({ ...value, secondStep: event.target.value })
    };

    return (
        <Grid item>
            <FormControl component="fieldset">
                <div className="question">Какой вариант для вас подходит?</div>
                <div className="completeTextContainer">По соотношению цена/качество </div>
                <RadioGroup aria-label="gender" name="gender1" value={value.secondStep} onChange={handleChange}>
                    <FormControlLabel value="cheap" control={<Radio />} label="Бюджетный" />
                    <FormControlLabel value="highQuality" control={<Radio />} label="Высококачественный" />
                    <FormControlLabel value="optimal" control={<Radio />} label="Оптимальный" />
                </RadioGroup>
            </FormControl>
        </Grid>
    )
}

export default SecondStepContent